import Vue from 'vue'
import Router from 'vue-router'
import NotFound from '../views/NotFound.vue'
import Regist from '../views/Regist.vue'
import store from './../store'

Vue.use(Router)

const router = new Router({
  linkExactActiveClass: 'nav-active',
  routes: [
    {
      path: '/',
      name: 'root',
      redirect: '/login'
    },
    {
      path: '/regist',
      name: 'regist',
      component: Regist
    },
    {
      path: '/login',
      name: 'login',
      component: () => import('./../views/Login.vue')
    },
    {
      path: '/setting',
      name: 'setting',
      component: () => import('./../views/Setting.vue')
    },
    {
      path: '/tweets/:tweet_id',
      name: 'tweet-reply',
      component: () => import('./../views/TweetReply.vue')
    },
    {
      path: '/tweets',
      name: 'tweets-main',
      component: () => import('./../views/TweetMain.vue')
    },
    {
      path: '/users/:id/followers',
      name: 'user-follower',
      component: () => import('./../views/UserFollower.vue')
    },
    {
      path: '/users/:id/followings',
      name: 'user-following',
      component: () => import('./../views/UserFollowing.vue')
    },
    {
      path: '/users/:id/tweet',
      name: 'user-profile-tweet',
      component: () => import('./../views/UserProfileTweets.vue')
    },
    {
      path: '/users/:id/reply',
      name: 'user-profile-reply',
      component: () => import('./../views/UserProfileReplies.vue')
    },
    {
      path: '/users/:id/like',
      name: 'user-profile-like',
      component: () => import('./../views/UserProfileLikes.vue')
    },
    {
      path: '/admin',
      name: 'admin-login',
      component: () => import('./../views/AdminLogin.vue')
    },
    {
      path: '/admin_tweets',
      name: 'admin-tweets',
      component: () => import('./../views/AdminTweets.vue')
    },
    {
      path: '/admin_users',
      name: 'admin-users',
      component: () => import('./../views/AdminUsers.vue')
    },
    {
      path: '/chatroom',
      name: 'chatroom',
      component: () => import('./../views/ChatRoom.vue')
    },
    {
      path: '*',
      name: 'not-found',
      component: NotFound
    }
  ]
})

router.beforeEach(async (to, from, next) => {
  const tokenInLocalStorage = localStorage.getItem('token')
  const tokenInStore = store.state.token
  let isAuthenticated = store.state.isAuthenticated
  console.log(store.state.isAuthenticated)

  if (tokenInLocalStorage && tokenInLocalStorage !== tokenInStore) {
    isAuthenticated = await store.dispatch('fetchCurrentUser')
  }
  const pathsWithoutAuthentication = ['admin-login', 'login', 'regist']
  if (!isAuthenticated && !pathsWithoutAuthentication.includes(to.name)) {
    next('/login')
    return
  }
  // if (isAuthenticated && pathsWithoutAuthentication.includes(to.name)) {
  //   next('/tweets')
  //   return
  // }
  next()
})
export default router
